.news {
  min-height: 100vh;
  background: url("../img/news__back.jpg") no-repeat center / 100% 100%;
  padding-top: 120px;
  padding-bottom: 300px;
}

.news__title {
  color: #ffffff;
  margin-bottom: 100px;
}

.news__item {
  display: flex;
  justify-content: space-between;
  column-gap: 120px;
  align-items: flex-start;
}

.news__img {
  width: 410px;
  flex-shrink: 0;
  border-radius: 20px;
  overflow: hidden;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.news__content {
  font-family: $font1;
  color: #ffffff;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: stretch;

  a {
    position: relative;

    &:hover {
      text-decoration: underline;
    }

    // &::after {
    //   position: absolute;
    //   content: "";
    //   bottom: 0;
    //   left: 0;
    //   right: 0;
    //   width: 100%;
    //   height: 1px;
    //   background: #ffffff;
    //   transform: scale(0);
    //   transition: all 0.3s;
    // }

    // &:hover::after {
    //   transform: scale(1);
    // }
  }

  h3 {
    font-weight: 600;
    margin-bottom: 15px;
  }

  p {
    font-weight: 400;
    margin-bottom: 15px;
  }
}

.news__tag-list {
  display: flex;
  align-items: center;
  margin-bottom: 15px;

  li {
    &:not(:last-of-type) {
      margin-right: 10px;
    }
  }
}
